import { useEffect, useState } from "react";
import {
  Box,
  Fab,
  Tooltip,
  Modal,
  styled,
  Typography,
  Button,
} from "@mui/material";
import { Add } from "@mui/icons-material";
import { useDispatch, useSelector } from "react-redux";
import { createTask } from "../features/tasks/taskSlice";
import moment from "moment";

const StyledModal = styled(Modal)({
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
});

const AddTask = () => {
  const [open, setOpen] = useState(false);

  const dispatch = useDispatch();

  const { isError, message } = useSelector((state) => state.tasks)

  useEffect(() => {
    if (isError) {
      console.log(message)
    }

  }, [isError, message, dispatch])

  const onSubmit = (e) => {
    e.preventDefault();

    const time_now = new Date();
    const time_later = moment(time_now).add(20, "m").toDate();

    dispatch(
      createTask({
        cleaners_assigned: ["6342be8918c92608984cb602"],
        cleaning_tasks: "wiping",
        room: 404,
        floor: 4,
        start_time: time_now.toISOString(),
        end_time: time_later.toISOString(),
      })
    );
  };

  return (
    <>
      <Tooltip title="Add">
        <Fab
          color="primary"
          aria-label="add"
          sx={{
            position: "fixed",
            bottom: 20,
            // minus border radius daw
            left: { xs: "calc(50%) - 25px", sm: 30 },
          }}
          onClick={() => setOpen(true)}
        >
          <Add />
        </Fab>
      </Tooltip>
      <StyledModal
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box
          sx={{
            width: 400,
            height: 280,
            background: "white",
            p: 3,
            borderRadius: 5,
          }}
        >
          <Typography variant="h6" color="gray" textAlign="center">
            Create a post
          </Typography>
          <Box component="form" onSubmit={onSubmit}>
            <Button type="submit" fullWidth variant="contained">
              Submit
            </Button>
          </Box>
        </Box>
      </StyledModal>
    </>
  );
};

export default AddTask;
