import axios from "axios";

const API_TASK = "http://localhost:8000/api/tasks";

const createTask = async (headData, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.post(API_TASK, headData, config);

  return response.data;
};

const getTasks = async (token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.get(API_TASK, config);

  return response.data;
};

const deleteTask = async (taskId, token) => {
  const config = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const response = await axios.delete(API_TASK, taskId, config);
};

const taskService = {
  createTask,
  getTasks,
  deleteTask,
};

export default taskService;
