import moment from "moment";

import { useDispatch } from "react-redux";
import { store } from "../app/store";
// import { deleteTask } from "../features/tasks/taskSlice";

import { DataGrid } from "@mui/x-data-grid";
import {
  Avatar,
  AvatarGroup,
  Chip,
  IconButton,
  styled,
  Tooltip,
} from "@mui/material";
import {
  Cached,
  Check,
  Close,
  Delete,
  Edit,
  HourglassEmpty,
  HourglassFull,
  PersonalVideo,
} from "@mui/icons-material";
import { Stack } from "@mui/system";

const DataGridAvatar = styled(Avatar)({
  width: "30px",
  height: "30px",
});

const columns = [
  // Task I.D.
  {
    field: "_id",
    headerName: "Task I.D.",
    sortable: false,
    renderCell: (params) => {
      return (
        <Tooltip title={params.value}>
          <span>{params.value.slice(-7)}</span>
        </Tooltip>
      );
    },
    width: 100,
  },

  // Cleaners assigned
  {
    field: "cleaners_assigned",
    headerName: "Cleaner(s) Assigned",
    sortable: false,
    renderCell: (params) => {
      return (
        <AvatarGroup max={4}>
          {params.value.map((cleaner) => (
            <Tooltip key={cleaner} title={cleaner}>
              <DataGridAvatar alt={cleaner} key={cleaner} />
            </Tooltip>
          ))}
        </AvatarGroup>
      );
    },
    width: 150,
  },

  // Cleaning tasks
  {
    field: "cleaning_tasks",
    headerName: "Cleaning Task(s)",
    renderCell: (params) => {
      const cleaning_tasks = params.value.split(",");

      return (
        <Stack direction="row" spacing={1}>
          {cleaning_tasks.map((task) => (
            // <ListItem key={defaultTasks.filter((task_) => task_.label == task).key}>
            <Chip size="small" label={task} />
            // </ListItem>
          ))}
        </Stack>
      );
    },
    width: 325,
  },

  // Additional Instructions
  {
    align: "center",
    field: "more_instructions",
    headerName: "More?",
    width: 70,
    renderCell: (params) => (params?.value ? <Check /> : <Close />),
  },

  // Room
  { field: "room", headerName: "Room", width: 70 },

  // Floor
  { field: "floor", headerName: "Floor", width: 70 },

  // Start time
  {
    field: "start_time",
    headerName: "Start Time",
    // valueFormatter: (params) => moment(params?.value).format("DD/MM  hh:mm A"),
    renderCell: (params) => {
      return (
        <Tooltip
          title={moment(params?.value).format("dddd, MMMM Do YYYY, h:mm a")}
        >
          <span>{moment(params?.value).format("DD-MM  hh:mm A")}</span>
        </Tooltip>
      );
    },
    width: 150,
  },

  // End time
  {
    field: "end_time",
    headerName: "End Time",
    // valueFormatter: (params) => moment(params?.value).format("DD/MM  hh:mm A"),
    renderCell: (params) => {
      return (
        <Tooltip
          title={moment(params?.value).format("dddd, MMMM Do YYYY, h:mm a")}
        >
          <span>{moment(params?.value).format("DD-MM  hh:mm A")}</span>
        </Tooltip>
      );
    },
    width: 150,
  },

  // Status
  {
    align: "center",
    field: "status",
    headerName: "Status",
    sortable: false,
    renderCell: (params) => {
      // tentative

      const start_time = new Date(params.row.start_time);
      const end_time = new Date(params.row.end_time);
      const now_time = new Date();

      if (now_time.valueOf() < start_time.valueOf()) {
        return (
          <Chip
            color="primary"
            icon={<HourglassEmpty />}
            label="Not started"
            size="small"
          />
        );
      } else if (
        now_time.valueOf() >= start_time.valueOf() &&
        now_time.valueOf() <= end_time.valueOf()
      ) {
        return (
          <Chip
            color="success"
            icon={<Cached />}
            label="On Going"
            variant=""
            size="small"
          />
        );
      } else {
        return <Chip icon={<HourglassFull />} label="Closed" size="small" />;
      }
    },
    width: 125,
  },

  // Actions
  {
    field: "actions",
    headerName: "Actions",
    sortable: false,
    renderCell: (params) => {
      const handleEdit = (e) => {
        e.stopPropagation(); // don't select this row after clicking
        alert(`Delete ko to: ${params.row._id}?`);
      };

      const handleDelete = (e) => {
        e.stopPropagation();
        console.log(params.row._id)
        store.deleteTask(params.row._id);
      };

      return (
        <>
          <IconButton
            onClick={handleEdit}
            aria-label="edit"
            color="primary"
            size="small"
          >
            <Edit fontSize="inherit" />
          </IconButton>
          <IconButton
            onClick={handleDelete}
            aria-label="delete"
            color="error"
            size="small"
          >
            <Delete fontSize="inherit" />
          </IconButton>
          <IconButton
            aria-label="live video"
            color="secondary"
            size="small"
            disabled
          >
            <PersonalVideo fontSize="inherit" />
          </IconButton>
        </>
      );
    },
  },
];

const TaskGrid = ({ tasks, selectionModel, handleSelectionChange }) => {
  const dispatch = useDispatch();

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
      }}
    >
      <div style={{ flexGrow: 1, height: 500, width: "80%" }}>
        <DataGrid
          checkboxSelection
          columns={columns}
          getRowId={(row) => row._id}
          onSelectionModelChange={handleSelectionChange}
          rows={tasks}
          selectionModel={selectionModel}
        />
      </div>
    </div>
  );
};

export default TaskGrid;
